import axios from "axios";
import router from "@/router";

const BASE_URL = "http://192.168.1.3:9877";
const axiosInstance = axios.create({
  baseURL: BASE_URL,
  timeout: 50000,
  headers: {
    "Content-Type": "application/json",
  },
});

const handleError = (error) => {
  let currentPath;
  if (error.response) {
    switch (error.response.status) {
      case 401:
        currentPath = router.currentRoute.value.fullPath;
        localStorage.setItem("redirect", currentPath);
        router.push(`/setting`);
        break;
      case 403:
        console.error("Access forbidden");
        break;
      case 404:
        console.error("Resource not found");
        break;
      case 500:
        console.error("Server error");
        break;
      default:
        console.error("An error occurred:", error.response.data);
    }
  } else if (error.request) {
    console.error("No response received:", error.request);
  } else {
    console.error("Error setting up request:", error.message);
  }
  return Promise.reject(error);
};

//  token
axiosInstance.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem("token"); //  token  localStorage 
    if (token) {
      config.headers["Authorization"] = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

axiosInstance.interceptors.response.use(
  (response) => response,
  (error) => handleError(error)
);

// GET 
export const get = async (endpoint, params = {}, customConfig = {}) => {
  try {
    const response = await axiosInstance.get(endpoint, {
      params,
      ...customConfig,
    });
    return response.data;
  } catch (error) {
    console.error("GET request error:", error);
    throw error;
  }
};

